.about-page {
  padding-top: 80px;
}

.hero-section {
  background-image: url('/a3.jpg');
  background-size: cover;
  background-position: center;
  height: 400px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  text-align: center;
}

.hero-content {
  max-width: 600px;
  margin: 0 auto;
}

.hero-content h1 {
  font-size: 48px;
  margin-bottom: 20px;
}

.hero-content p {
  font-size: 24px;
}

.section {
  padding: 80px 0;
}

.section h2 {
  font-size: 36px;
  margin-bottom: 30px;
}

.section p {
  font-size: 18px;
  line-height: 1.6;
  margin-bottom: 20px;
}

.section ul {
  list-style-type: disc;
  margin-left: 20px;
  margin-bottom: 20px;
}

.section ul li {
  font-size: 18px;
  line-height: 1.6;
}

.bg-light {
  background-color: #f8f9fa;
}

.img-fluid {
  max-width: 100%;
  height: auto;
}